/* ===============divButton============ */
.btn {
	position: relative;
    width: 50%;
    display: inline-block;
	padding: 20px 20px;
	font-size: 1rem;
	color: black;
	letter-spacing: 1.1rem;
	text-transform: uppercase;
	transition: all 500ms cubic-bezier(0.77, 0, 0.175, 1);
	cursor: pointer;
	user-select: none;
}

.btn:before, .btn:after {
	content: '';
	position: absolute;
	transition: inherit;
	z-index: -1;
}

.btn:hover {
	color: white;
	transition-delay: .5s;
}

.btn:hover:before {
	transition-delay: 0s;
}

.btn:hover:after {
	background: black;
	transition-delay: .35s;
}

.from-top:before,
.from-top:after {
	left: 0;
	height: 0;
	width: 100%;
}

.from-top:before {
	bottom: 0;
	border: 1px solid black;
    border-bottom: 0;
    border-right: 0;
    border-left: 0;
}

.from-top:after {
	top: 0;
	height: 0;
}

.from-top:hover:before,
.from-top:hover:after {
	height: 100%;
}

*, *:before, *:after {
	box-sizing: border-box;
}
